body {
  background-color: #1e1e1e;
  color: #ffffff;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

input[type="text"], button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #3c3c3c;
  color: #ffffff;
  font-size: 18px;
  width: 300px;
}

input[type="text"] {
  margin-bottom: 10px;
}

button {
  cursor: pointer;
}

button:hover {
  background-color: #7c7c7c;
}

p {
  font-size: 20px;
  text-align: center;
  margin-bottom: 20px;
}
